.biotune-container {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0px 60px;
}

.biotune-image {
	width: 556px;
	height: 358px;
}

.biotune-logo {
	width: 106px;
}
.biotune-text-container {
	display: flex;
	flex-direction: column;
	width: 672px;
	gap: 32px;
}

.biotune-logo-wrapper {
	padding-left: 60px;
	padding-bottom: 32px;
	width: 90%;
	max-width: 1540px;

	display: flex;
	justify-content: flex-start;
}
