.hero-container {
	padding: 0px 60px;
	justify-content: space-between;
	display: flex;
	align-items: center;
}
.hero-text-container {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	gap: 32px;
	width: 690px;
}

.hero-p-wrapper {
	width: 690px;
}

.hero-buttons-container {
	display: flex;
	flex-direction: row;
	gap: 16px;
}

.hero-video-wrapper {
	height: 530px;
	width: 498px;
}

.hero-vbutton-wrapper {
	position: absolute;
	display: flex;
	justify-content: flex-end;
	z-index: 2;
}
