.contactUs-container {
	display: flex;
	justify-content: space-between;
}

.contactUs-video-wrapper {
	display: flex;
	width: 50%;
	overflow: hidden;
	height: auto;
}

.contactUs-video {
	height: 735px;

	margin-left: -500px;
}

.contactus-form-container {
	display: flex;
	flex-direction: column;
	width: 680px;
	max-width: 680px;

	padding: 40px 60px;
	align-self: stretch;
	gap: 48px;
}

.form-firstLast-container {
	display: flex;
	gap: 20px;
	align-self: stretch;
	width: 100%;
	justify-content: flex-start;
}

.form {
	display: flex;
	flex-direction: column;
	gap: 12px;
}

.form-input-wrapper {
	width: 100%;

	textarea {
		align-items: flex-start;
		gap: 10px;

		width: 100%;
		height: 227px;
		border-radius: 8px;
		border: none;
		background: var(--generic-white, #fff);
		/* Drop shadow/XSmall */

		color: #2e2e2e;
		/* Paragraph/Small/Regular */
		font-family: Inter;
		font-size: 14px;
		font-style: normal;
		font-weight: 400;
		line-height: 20px;

		resize: none;
		&:hover {
			outline: none;
		}
		&:focus {
			outline: none;
		}

		&::placeholder {
			color: #2e2e2e;
			/* Paragraph/Small/Regular */
			font-family: Inter;
			font-size: 14px;
			font-style: normal;
			font-weight: 400;
			line-height: 20px; /* 142.857% */
		}
	}

	p {
		color: red;
		margin-top: 6px;
		background: #ffe1e1;
		border-radius: 4px;
		background-color: #ffe1e1;
		width: fit-content;
		display: flex;
		padding: 4px;
		justify-content: center;
		align-items: center;
		gap: 10px;
	}

	.hidden {
		display: none;
	}
}

////////////////////////////////
input {
	display: flex;
	min-width: 50%;
	border: none;
	color: #2e2e2e;
	/* Paragraph/Small/Regular */
	font-family: Inter;
	font-size: 14px;
	font-style: normal;
	font-weight: 400;
	line-height: 20px;

	&:hover {
		outline: none;
	}
	&:focus {
		outline: none;
		$active: true;
	}

	&::placeholder {
		color: #2e2e2e;
		/* Paragraph/Small/Regular */
		font-family: Inter;
		font-size: 14px;
		font-style: normal;
		font-weight: 400;
		line-height: 20px; /* 142.857% */
	}
}

.full-input {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-start;
	display: flex;
	padding: 17px 12px;
	min-width: 50%;

	border-radius: 8px;
	border: 1px solid rgba(0, 0, 0, 0.1);
	background: var(--generic-white, #fff);
	/* Drop shadow/XSmall */
	box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05);

	&:hover {
		outline: solid 1px #5498ee;
		box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05),
			0px 0px 0px 4px rgba(84, 152, 238, 0.1);
	}

	label {
		display: none;
	}

	.contactUs-inputplace-container {
		display: flex;
		justify-content: space-between;
		width: 100%;
		a {
			color: #737373;
			/* Paragraph/Small/Regular */
			font-family: Inter;
			font-size: 14px;
			font-style: normal;
			font-weight: 400;
			line-height: 20px;
		}
	}

	.contactUs-correct-icon {
		display: none;
	}
}

.full-input-active {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	display: flex;
	padding: 8px 12px;

	border-radius: 8px;
	border: 1px solid #5498ee;
	background: var(--generic-white, #fff);
	/* Drop shadow/XSmall */
	box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05);

	&:hover {
		outline: solid 1px #5498ee;
		box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05),
			0px 0px 0px 4px rgba(84, 152, 238, 0.1);
	}

	label {
		padding-left: 1px;
		color: #737373;
		font-family: Inter;
		font-size: 12px;
		font-style: normal;
		font-weight: 400;
		line-height: 16px; /* 133.333% */
	}

	.contactUs-inputplace-container {
		display: flex;
		justify-content: space-between;
		a {
			display: none;
		}
	}
	.contactUs-correct-icon {
		display: none;
	}
}

