	}

	label {
		padding-left: 1px;
		color: #737373;
		font-family: Inter;
		font-size: 12px;
		font-style: normal;
		font-weight: 400;
		line-height: 16px; /* 133.333% */
	}
	.contactUs-inputplace-container {
		display: flex;
		justify-content: space-between;
		a {
			display: none;
		}
	}

	.contactUs-correct-icon {
		display: flex;
	}
}

.full-input-error {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	display: flex;
	padding: 8px 12px;

	border-radius: 8px;
	border: 1px solid #ef4444;
	background: var(--generic-white, #fff);
	/* Drop shadow/XSmall */
	box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05);

	&:hover {
		outline: solid 1px #5498ee;
		box-shadow: 0px 1px 2px 0px rgba(16, 24, 40, 0.05),
			0px 0px 0px 4px rgba(84, 152, 238, 0.1);
	}

	label {
		padding-left: 1px;
		color: #737373;
		font-family: Inter;
		font-size: 12px;
		font-style: normal;
		font-weight: 400;
		line-height: 16px; /* 133.333% */
	}
	.contactUs-inputplace-container {
		display: flex;
		justify-content: space-between;
		a {
			display: none;
		}
	}

	.contactUs-correct-icon {
		display: none;
	}
}

.hidden {
	display: none;
}

.error-message {
	display: flex;
	padding: 4px;
	justify-content: center;
	align-items: center;
	gap: 10px;

	border-radius: 4px;
	background: #ffe1e1;

	color: #ef4444;
	/* Paragraph/Small/Regular */
	font-family: Inter;
	font-size: 14px;
	font-style: normal;
	font-weight: 400;
	line-height: 20px; /* 142.857% */
}

